.category-page {
  margin-bottom: 160px;
}

.category-page .title {
  margin-bottom: 20px;
}

.equipment .pagination {
  display: none;
}

.equipment .category__item {
  width: 25%;
  margin-bottom: 0;
  max-width: 280px;
  border: none;
  border-radius: 0;
  padding: 0 15px;
}

.equipment .category__item a {
  padding-bottom: 30px;
}

.equipment .category-page__content {
  padding: 0;
  margin-left: -15px;
  margin-right: -15px;
  justify-content: flex-start;
}

.no-delivery {
  color: var(--red);
  text-transform: uppercase;
}

.catalog__content {
  margin-top: 40px;
  margin-left: -15px;
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
}

.catalog__title {
  margin-bottom: 40px;
}

.catalog__img {
  margin-bottom: 2px;
}

.catalog__img img {
  max-width: 100%;
}

.catalog__img a {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.catalog img {
  width: 100%;
}

.show-pointer:before {
  content: "";
  position: absolute;
  right: 10px;
  bottom: 15px;
  width: 34px;
  height: 40px;
  z-index: 999;
  background-size: 34px 40px;
  background-image: url(data:image/gif;base64,);
}

.outofstock .catalog__details {
  filter: grayscale(100%);
}

.outofstock .cart-btn {
  opacity: 0.25;
  pointer-events: none !important;
  cursor: default;
}

.catalog__price {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.catalog__old-price {
  width: 50%;
  text-align: center;
  text-decoration: line-through;
  font-size: 19px;
  font-weight: 300;
}

.catalog__new-price {
  color: var(--red);
  width: 50%;
  text-align: center;
  font-size: 24px;
  font-weight: 600;
}

.catalog__btn {
  display: flex;
  align-items: stretch;
  justify-content: space-between;
}

.category-page__content {
  padding: 0 15px;
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  flex-wrap: wrap;
}

/*--------------GEL CATEGORY-------------------*/
.gel_category .category-page__content {
  margin-left: 0;
  margin-right: 0;
  padding: 0 15px;
  justify-content: space-between;
}

.gel_category .category__item {
  margin-bottom: 0;
  padding: 0 15px;
  border: none;
  border-radius: 0;
  width: calc(50% - 20px);
  max-width: 532px;
}

.gel_category .category__name {
  padding-bottom: 30px;
  height: auto;
  font-size: 20px;
}

@media (max-width: 1200px) {
  .category-page {
    margin-bottom: 32px;
  }
  body .catalog__item {
    width: 33.3333%;
  }
}

@media (max-width: 991px) {
  .category-page__content {
    padding: 0;
  }
  body .catalog__item {
    margin: 0 0 30px;
    width: calc(33.3333% - 15px);
  }
  body .catalog__item .products__marker div {
    font-size: 13px;
  }
  body .catalog__content {
    justify-content: space-between;
    margin-left: 0;
  }
  body .catalog__name {
    height: auto;
  }
  body .catalog__info {
    height: fit-content;
  }
  html[lang=es-ES] .cart-btn {
    font-size: 16px;
  }
}

@media (max-width: 768px) {
  .gel_category .category__item {
    width: 100% !important;
    max-width: 80%;
  }
  .category-page__content,
  .gel_category .category-page__content {
    justify-content: center;
  }
}

@media (max-width: 767px) {
  body .catalog__item {
    width: calc(50% - 15px);
  }
  .gel_category .category-page__content {
    margin-left: -15px;
    margin-right: -15px;
    padding: 0;
  }
}

@media (max-width: 600px) {
  .category-page .title {
    margin-bottom: 10px;
  }
  body .catalog__content {
    margin-top: 20px;
  }
  body .catalog__title {
    margin-bottom: 20px;
  }
  body .catalog__item {
    width: 48%;
    margin-right: auto;
    margin-left: auto;
    max-width: 280px;
  }
  body .catalog__price {
    justify-content: space-around;
  }
  body .catalog .products__marker div {
    width: 75%;
  }
  .cart-btn {
    font-size: 14px;
  }
  .equipment .category__item {
    width: 48%;
  }
  .category-page .catalog__content {
    margin-left: -15px;
    margin-right: -15px;
  }
  .equipment .category-page__content {
    justify-content: center;
  }
}

@media (max-width: 576px) {
  .catalog__item .products__marker div {
    width: 75%;
  }
  html[lang=es-ES] .cart-btn {
    font-size: 12px;
  }
  .equipment .category__item {
    margin-bottom: 16px;
  }
}

@media (max-width: 500px) {
  .gel_category .category__item {
    max-width: 100%;
  }
  .category-page__content {
    padding: 0;
  }
}

@media (max-width: 420px) {
  body .catalog__item {
    width: calc(50% - 10px);
  }
  body .catalog__item .products__marker div {
    width: 95%;
  }
  .cart-btn,
  .catalog__item .products__marker div {
    font-size: 12px;
  }
}

@media (max-width: 400px) {
  html[lang=es-ES] .cart-btn {
    text-transform: unset;
  }
}
